import Head from "next/head"
import { style } from "typestyle"
import fetch from "isomorphic-unfetch"

import HStack from "../../components/HStack"

import Header from "../../components/Header"
import Footer from "../../components/Footer"

import ResponsiveContainer from "../../components/ResponsiveContainer"
import Spacer from "../../components/Spacer"
import MarkdownView from "../../components/MarkdownView"

declare var __NEXT_DATA__: any

const s = {
    titleContainer: style({
        width: "100%",
        marginBottom: "30px"
    }),
    title: style({
        margin: "0 0 10px 0",
        fontSize: "3rem"
    }),
    meta: style({
        width: "100%",
        borderBottom: "1px dashed rgba(0, 0, 0, 0.4)"
    }),
    category: style({
        fontWeight: "bold",
        lineHeight: "2rem"
    }),
    date: style({
        marginLeft: "10px",
        lineHeight: "2rem"
    }),
    ert: style({
        marginLeft: "10px",
        lineHeight: "2rem"
    })
}

const ArticleItem = ({ article, date }) => {
    return (
        <div className="main-container">
            <Head>
                <title>{article.meta.title} | YiqinZhao</title>
            </Head>

            <Header active="articles" />

            <ResponsiveContainer>
                <Spacer />
                <div className={s.titleContainer}>
                    <h1 className={s.title}>
                        {article.meta.title}
                    </h1>
                    <HStack className={s.meta}>
                        <div className={s.category}>
                            {article.meta.category}
                        </div>
                        <div className={s.date}>
                            {article.meta.date}
                        </div>
                        <div className={s.ert}>
                            {article.meta.date}
                        </div>
                    </HStack>
                </div>
                <Spacer />
            </ResponsiveContainer>

            <ResponsiveContainer>
                <Spacer />
                <MarkdownView className="body" content={article.body} />
                <Spacer />
            </ResponsiveContainer>
            <Footer date={date} />
        </div>
    )
}


ArticleItem.getInitialProps = async ({ req, query }) => {
    if (req) {
        const dayjs = require("dayjs")
        const date = dayjs().format("MMM D, YYYY")

        const res = await fetch(
            "http://localhost:3000/api/articles?name="
            + encodeURIComponent(query.name)
        )
        let article = await res.json()
        return { article, date }
    } else {
        return __NEXT_DATA__.props.pageProps
    }
}

export default ArticleItem
